<!-- eslint-disable vue/no-template-shadow -->
<template>
	<v-form id="form">
		<p class="text">
			 Facebook ,      
			 ,       
		</p>
		<section
			v-if="isHeader"
			id="header-example"
		>
			<span class="d-block label"></span>
			<span class="label">{{ headerTextAndFormats.example }}</span>
			<v-file-input
				v-model="files"
				placeholder=" "
				:accept="headerTextAndFormats.acceptedFormats"
				outlined
				:rules="[
					value => !value || value.size < 5000000 || '    5',
				]"
				:show-size="1000"
			>
				<template #selection="{ index, text }">
					<v-chip
						v-if="index < 2"
						color="blue accent-3"
						dark
						label
						small
					>
						{{ text }}
					</v-chip>
				</template>
			</v-file-input>
		</section>
		<section
			v-if="isText"
			id="content-example"
		>
			<span class="label"></span>
			<v-col class="content-example__content">
				<span class="text">  (       )</span>
				<div class="editable-container">
					<span
						v-for="(part, index) in parsedText"
						:key="index"
						:class="part.type === 'text' ? 'text' : 'editable-placeholder-container'"
					>
						{{ part.value }}
						<span
							v-if="part.type === 'placeholder'"
							class="editable-placeholder"
							contenteditable
							:data-index="part.index"
							@input="onInput($event.target.textContent, part.index)"
						/>
					</span>
				</div>
			</v-col>
		</section>
		<section
			v-if="isUrl"
			id="button-example"
		>
			<span class="d-block label">  </span>
			<span class="d-block mb-2 text">    </span>
			<span class="d-block mb-2 label"> : <b>{{ button.text }}</b></span>
			<p class="ma-0 text">
				  URL-,      . : <b>https://weblink.com/1</b>.  <b>https://weblink.com/</b> -   ,     . <b>1</b> -    .
			</p>
			<v-text-field
				label="URL"
				clearable
			/>
		</section>
	</v-form>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
	props: {
		isHeader: {
			type: Boolean,
			default: false,
		},
		headerType: {
			type: String,
			default: ''
		},
		isText: {
			type: Boolean,
			default: false,
		},
		isUrl: {
			type: Boolean,
			default: false,
		},
	},
	data() {
		return {
			image: true,
			button: {
				text: '  ',
			},
			uploadOption: 'fromComputer',
			text1: '{{1}},     edna - {{2}}.     ,    {{3}}',
			textExampleParams: [],
			files: null,
			fileUrl: ''
		};
	},
	computed: {
		...mapGetters([
			'getTemplate',
			'getContentSchema'
		]),
		parsedText() {
			const text = this.storeTemplate.content.text;
			const regex = /\{\{(\d+)\}\}/g;
			const parts = [];
			let lastIndex = 0;
			let match;
			let placeholderIndex = 0;

			while ((match = regex.exec(text)) !== null) {
				const textBeforeMatch = text.slice(lastIndex, match.index);
				if (textBeforeMatch) {
					parts.push({ type: 'text', value: textBeforeMatch });
				}

				parts.push({ type: 'placeholder', value: '', index: placeholderIndex });

				placeholderIndex++;
				lastIndex = regex.lastIndex;
			}

			const remainingText = text.slice(lastIndex);
			if (remainingText) {
				parts.push({ type: 'text', value: remainingText });
			}

			return parts;
		},
		storeTemplate() {
			return this.getTemplate;
		},
		templateSchema() {
			return this.getContentSchema;
		},
		headerTextAndFormats() {
			if(this.headerType === 'IMAGE') {
				return {
					example: '   (.jpg, .jpeg  .png  5)',
					exampleSubtitle: '   ',
					acceptedFormats: '.png, .jpeg, .jpg',
				};
			}
			if (this.headerType === 'VIDEO') {
				return {
					example: '   (.mp4  .3gpp  5)',
					exampleSubtitle: '   ',
					acceptedFormats: '.mp4, .3gpp',
				};
			}
			if (this.headerType === 'DOCUMENT') {
				return {
					example: '   (.pdf  5)',
					exampleSubtitle: '   ',
					acceptedFormats: '.pdf',
				};
			}
			return {};
		},
	},
	mounted() {
		this.$store.commit('setTextWithParams', this.replaceVariables(this.storeTemplate.content.text, this.textExampleParams));
	},
	methods: {
		onInput(value, index) {
			this.textExampleParams[index] = value;
			this.$store.commit('setTextWithParams', this.replaceVariables(this.storeTemplate.content.text, this.textExampleParams));
		},
		replaceVariables(text, params) {
			return text.replace(/\{\{(\d+)\}\}/g, (match, index) => {
				const paramIndex = parseInt(index) - 1;
				return params[paramIndex] !== undefined ? params[paramIndex] : match;
			});
		},
	}
};
</script>

<style lang="scss" scoped>
	.title {
		font-size: 16px;
		color: #121212;
	}
	.label {
		display: block;
		margin-bottom: 3px;	
		font-size: 14px;
		color: rgb(91, 91, 91);
	}
	.text {
		font-size: 12px;
		color: rgb(91, 91, 91);
	}
	#form {
		cursor: default;
	}
	.editable-placeholder-container {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 7px;
		line-height: 40px;
		cursor: text;
	}
	.editable-placeholder {
		width: 150px;
		padding: 0 10px;
		border-radius: 5px;
		text-align: center;
		overflow: hidden;
		background: rgba(0, 0, 0, 0.06);
		white-space: nowrap;
	}
	.content-example__content {	
		padding: 10px;
		border: 1px solid rgba(0, 0, 0, 0.06);
		border-radius: 5px;
	}
</style>
